/* asap-condensed-400normal - latin */
@font-face {
  font-family: 'Asap Condensed';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url('./files/asap-condensed-latin-400.eot'); /* IE9 Compat Modes */
  src:
    local('Asap Condensed Regular '),
    local('Asap Condensed-Regular'),
    url('./files/asap-condensed-latin-400.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('./files/asap-condensed-latin-400.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/asap-condensed-latin-400.woff') format('woff'), /* Modern Browsers */
    url('./files/asap-condensed-latin-400.svg#AsapCondensed') format('svg'); /* Legacy iOS */
}

